<template>
  <div id="login" ref="login">
    <div class="goback" @click="$router.go(-1)">
      <img src="../../assets/global/return@3x.png" alt="">
    </div>
    <div class="logo">
      <img src="../../assets/me/logo@3x.png" alt="">
    </div>
    <div class="loginType" @click="login('wechat')">
      <img src="../../assets/login/wechat@3x.png" alt="">
      <span>微信登录</span>
    </div>
    <div class="loginType" @click="login('zhifubao')">
      <img src="../../assets/login/alipay@3x.png" alt="">
      <span>支付宝登录</span>
    </div>
    <div class="loginType phone" @click="login('phone')">
      <img src="../../assets/login/mobilephone@3x.png" alt="">
      <span>手机号登录</span>
    </div>
    <!-- 手机号登录 -->
    <messageBox setHeight="250" :marginTop="marginTop" setType="confirm" v-show="messageBoxType === 'phonelogin'" ref="messgBox">
        <div slot="confirmColumn" class="common_alert">
            <div class="alert_icon">
              <span>手机号登录</span>
              <img src="../../assets/login/关闭拷贝@3x.png" alt="" @click="alertChoose(0)">
            </div>
            <div class="alert_describe">
              <div class="loginformat">
                <span>手机号</span>
                <input
                  type="tel" placeholder="" v-model="phoneNumber" @input="phoneVolidate"
                  @focus="keyCoded = !keyCoded" @blur="keyCoded = !keyCoded" maxlength="11">
              </div>
              <div class="loginformat">
                <span>验证码</span>
                <input
                  type="tel" placeholder="" v-model="phoneCode" @input="codeVolidate"
                  @focus="keyCoded = !keyCoded" @blur="keyCoded = !keyCoded" maxlength="6">
              </div>
              <span @click="getCheckCode" :class="{disalbedClick: !coded}">{{codeText}}</span>
            </div>
            <div class="alert_operation">
            <div class="determine_one" @click="alertChoose(1)" :class="{disalbedClick: !buttoned}">登录</div>
            </div>
        </div>
    </messageBox>
  </div>
</template>
<script>
// import {setStore, getStore} from '@/utils/index'
import {mapActions, mapGetters, mapState} from 'vuex'
import messageBox from '../../components/common/messageBox'
import head from '../../components/common/head'

import {
  getMobilePhoneCodeRequest,
  mobileLoginRequest,
  alipayLoginRequest,
  wechatLoginRequest
} from '../../api/method'
import {
  getMobilePhoneCodeResponse,
  mobileLoginResponse,
  alipayLoginResponse,
  wechatLoginResponse
} from '../../api/getData'
export default {
  data () {
    return {
      emailVal: null, // 帐号
      password: null,
      remember: false, // 默认不记住密码
      showPassword: true, // 默认不显示密码
      fullHeight: null, // 屏幕高度
      keyboard: false, // 软键盘是否弹出
      offsetTop: 0, // 偏移量（当软键盘遮住输入框偏移)
      transitionName: null, // 页面过度动画
      loginStateus: true, // 登录状态
      phoneNumber: null,
      phoneCode: null, // 验证码
      coded: false,
      codeText: '验证码',
      buttoned: false,
      marginTop: '-40%',
      keyCoded: false
    }
  },
  components: {
    'head-vue': head,
    messageBox
  },
  computed: {
    ...mapGetters(['messageType', 'maqttState']),
    ...mapState(['ToastType']),
    disabled () {
      // 检测email 密码是否为空
      if (this.emailVal && this.password) {
        return true
      } else {
        return false
      }
    },
    messageBoxType () {
      return this.messageType
    }
  },
  mounted () {
    // this.showMessagesBox({type: 'loginFailed'})
    this.$nextTick(() => {
      this.$refs.login.style.height = document.documentElement.clientHeight + 'px'
    })
    getMobilePhoneCodeResponse(this.NAVIGATOR, this)
    mobileLoginResponse(this.NAVIGATOR, this)
    alipayLoginResponse(this.NAVIGATOR, this)
    wechatLoginResponse(this.NAVIGATOR, this)
  },
  methods: {
    ...mapActions(['showMessagesBox', 'showLoading']),
    goForgot () {
      // 忘记密码
      this.$router.push({path: '/login/forgot'})
    },
    goRegsister () {
      // 注册
      this.$router.push({path: '/login/register'})
    },
    login (type) {
      // 登录
      switch (type) {
        case 'wechat':
          wechatLoginRequest(this.NAVIGATOR, this)
          if (this.$root.ISDEBUG) {
            window.wechatLoginResponse(true)
          }
          break
        case 'zhifubao':
          alipayLoginRequest(this.NAVIGATOR, this)
          break
        case 'phone':
          this.showMessagesBox({'type': 'phonelogin'})
          break
        default:
          break
      }
    },
    alertChoose (num) {
      switch (num) {
        case 0:
          this.showMessagesBox({'type': 'null'})
          break
        case 1:
          if (this.phoneNumber === null || this.phoneCode === null || this.phoneCode === '') {
            this.$store.state.ToastType = 'errorPhoneorCode'
            return false
          }
          mobileLoginRequest(this.NAVIGATOR, this, this.phoneNumber, this.phoneCode)
          if (this.$root.ISDEBUG) {
            window.mobileLoginResponse(true)
          }
          this.showMessagesBox({'type': 'null'})
          this.showLoading(true)
          break
        default:
          break
      }
    },
    getCheckCode () {
      if (!this.coded) {
        return false
      }
      getMobilePhoneCodeRequest(this.NAVIGATOR, this, this.phoneNumber, 1)
      if (this.$root.ISDEBUG) {
        window.getMobilePhoneCodeResponse(true)
      }
    },
    phoneVolidate () {
      if ((/^1[364578]\d{9}$/.test(this.phoneNumber))) {
        this.coded = true
        this.emailActive = true
      } else {
        this.coded = false
        this.emailActive = false
      }
    },
    codeVolidate () {
      if ((/^\d+$/.test(this.phoneCode)) && (/^1[364578]\d{9}$/.test(this.phoneNumber))) {
        this.buttoned = true
      } else {
        this.buttoned = false
      }
    }
  },
  watch: {
    // $route (to, from, next) {
    //   if (to.name === 'register') {
    //     this.transitionName = 'slide-up'
    //   } else {
    //     this.transitionName = 'slide-left'
    //   }
    // }
    keyCoded (newval) {
      if (newval) {
        this.marginTop = '-50%'
      } else {
        this.marginTop = '-40%'
      }
    }
  }
}
</script>
<style lang="less" scoped>
@import "../../styles/mixin.less";
#login{
  .wh(100%, 100%);
  background: url(../../assets/login/bg.png) no-repeat;
  // background: #000;
  background-size: 100% 100%;
  display: flex;
  flex-direction: column; align-items: center;
  color: #fbfbf9;
  .goback{
    margin-top: 73px;
    .wh(100%, 40px);
    padding-left: 53px;
    display: flex;
    img{
      .wh(20px, 100%)
    }
  }
  .logo{
    margin-top: 120px;
    margin-bottom: 137px;
    .wh(171px, 54px);
    align-self: center;
    img{
      .wh(100%, 100%);
    }
  }
  .loginType{
    .wh(77%, 94px);
    align-self: center;
    display: flex;
    justify-content: center;
    margin-bottom: 75px;
    background: @themeColor;
    border-radius: 10px;
    img{
      max-width: 51px;
      max-height: 42px;
      align-self: center;
      margin-right: 20px;
    }
    span{
      font-size: 30px;
      color: #ffffff;
      align-self: center;
    }
  }
  .phone{
    background: transparent;
    border: 1px solid #ffffff;
    img{
      margin-right: 30px;
    }
  }
  /deep/ .alert{
      width: 649px;
      margin-left: -325px;
    }
  .common_alert{
    /deep/.determine_one{
      .wh-l(522px,88px);
      margin: 0 auto;
      background: #aaaaaa;
      border-radius: 50px;
      background: @themeColor;
      margin-bottom: 73px;
    }
    .disalbedClick{
      background: #D6D6D6;
    }
    .alert_icon{
      margin-top: 41px;
      overflow: hidden;
      img{
        .wh(28px, 28px);
        float: right;
        margin-right: 33px;
      }
    }
    .alert_describe{
      margin-top: 5px;
      color: #303030;
      width: 522px;
      margin: 0 auto;
      .loginformat{
        .wh(522px, 96px);
        display: flex;
        border-bottom: .5px solid #a9a9a9;
        margin-top: 10px;
        padding-top: 20px;
        span{
          align-self: center;
          margin-right: 44px;
        }
      }
      input{
        flex: 1;
        height: 72px;
        align-self: flex-end;
        margin-bottom: 2px;
      }
      position: relative;
      >span{
        diplay: block;
        position: absolute;
        right: 20px;
        .wh-l(138px, 58px);
        background: @themeColor;
        border-radius: 50px;
        bottom: 8px;
        color: #ffffff;
      }
      .disalbedClick{
        background: #D6D6D6;
      }
    }
  }
}
</style>



